import React, {Component} from "react";
import {connect} from "react-redux";
import _ from "lodash";

import {voipSelection} from "../../../../fix/actions/voip";
import ModalService from "../../../../../flux/utils/OraModalService";
import OfferDetailsModal from "../shared/productDetails/OfferDetailsModal";
import {ChangeComponent, NumberComponent} from "./Utils";
import {voipModalComponentIsMounted} from "../../../../fix/selectors/root"
import {findDeviceByType} from "../../../analyzer/DeviceUtils";
import CommonPropositionEntry from "../shared/CommonPropositionEntry"
import {changeFixTvFilteredModalVisibility, changeTvModalVisibility, changeB2BVasesModalVisibility} from "../../../actions/cart";
import FixTvFilteredModal from "../../../../fix/components/modal/FixTvFilteredModal";
import {isRemovable} from "../utils/ConfigurableUtils";

class FixMainProductComponent extends Component {

    constructor(props) {
        super(props);
        this.prepareVoipEntry = this.prepareVoipEntry.bind(this);
        this.handleShowModalVoip = this.handleShowModalVoip.bind(this);
        this.onDetailsClicked = this.onDetailsClicked.bind(this);
        this.name = this.props.entry.bundleNo + "_" + this.props.entry.entryNo;
    }

    componentDidUpdate() {
        if (this.props.entry && this.props.configurables && this.props.configurables.length > 0) {
            let configurables = this.props.configurables[0];
            ModalService.add((data) => {

                //TODO workaround - consider populating devices on broadband and tv products as for landing page
                const broadbandDevices = this.props.entry.broadbandFixProduct.devices.map(device => device.code);
                const broadband = Object.assign({}, this.props.entry.broadbandFixProduct);
                broadband.devices = configurables.devices.filter(device => broadbandDevices.includes(device.id));

                let tvProduct = null;
                if(this.props.entry.tvFixProduct){
                    const tvDevices = this.props.entry.tvFixProduct.devices.map(device => device.code);
                    tvProduct = Object.assign({}, this.props.entry.tvFixProduct);
                    tvProduct.devices = configurables.devices.filter(device => tvDevices.includes(device.id));
                }

                return (
                    <OfferDetailsModal
                        id={"productDetails-" + this.name}
                        header={this.props.detailsHeader || "Szczegóły oferty"}
                        broadband={broadband}
                        tv={tvProduct}
                        voip={this.props.entry.voipFixProduct}
                        tvPackages={configurables.tvPackages}
                    />)
            });
        }
    }

    onDetailsClicked() {
        ModalService.open("productDetails-" + this.name);
    }

    handleShowModalVoip(e) {
        e.preventDefault();
        this.props.openVoipModal();
    }

    addButtonClicked(event){
        event.preventDefault();
        this.props.showTvModal();
    }

    showTvPackagesButtonClicked(event){
        event.preventDefault();
        this.props.showFixTvFilteredModal();
    }

    showB2BVasesButtonClicked(event) {
        event.preventDefault();
        this.props.showB2BVasesModal();
    }

    removeB2BVasesButtonClicked(event) {
        event.preventDefault();
        this.props.onVasRemove(this.props.entry.vasPackagesB2BFixProduct.code);
    }

    prepareTvEntry() {

        const tvChangeComponent = <ChangeComponent onChange={this.addButtonClicked.bind(this)} changeLabel="Zmień"/>;
        const tvPackagesModal = <ChangeComponent onChange={this.showTvPackagesButtonClicked.bind(this)} changeLabel="Pakiety telewizji"/>;
        let additionalContent=<div className={"l-row"}><div className={"l-col l-col--auto"}>{tvChangeComponent}</div>
            <div className={"l-col l-col--auto"}>{tvPackagesModal}{<FixTvFilteredModal/>}</div></div>;
        return {...this.props.entry.tvFixProduct, additionalContent: additionalContent};
    }

    prepareVoipEntry() {
        const voipNumberContent = <NumberComponent
            product={this.props.entry.voipFixProduct}
            numberLabel={this.props.sectionConf.numberLabel}
            selectedNumberVoip={this.props.selectedNumberVoip}
            changeNumberLabel={this.props.sectionConf.changeNumberLabel}
            handleShowModalVoip={this.handleShowModalVoip}
            editable={this.props.voipModalComponentIsMounted}
        />;
        return {...this.props.entry.voipFixProduct, additionalContent: voipNumberContent};
    }

    prepareVasPackagesB2BEntry() {
        const mockVasPackagesB2BDetailsComponent = <ChangeComponent onChange={this.addButtonClicked.bind(this)} changeLabel="Szczegóły"/>;
        const vasPackagesB2BChangeComponent = <ChangeComponent onChange={this.showB2BVasesButtonClicked.bind(this)} changeLabel="Zmień"/>;
        const vasPackagesB2BRemoveComponent = <ChangeComponent onChange={this.removeB2BVasesButtonClicked.bind(this)} changeLabel="Usuń"/>;
        let additionalContent=<div className={"l-row"}>
            <div className={"l-col l-col--auto"}>{mockVasPackagesB2BDetailsComponent}</div>
            <div className={"l-col l-col--auto"}>{vasPackagesB2BChangeComponent}</div>
            <div className={"l-col l-col--auto"}>{vasPackagesB2BRemoveComponent}</div>
        </div>
        return {...this.props.entry.vasPackagesB2BFixProduct, additionalContent: additionalContent};
    }

    isDeviceChangeable(availableDevices, mandatoryProducts, product) {
        const devicesWithSameType = availableDevices && findDeviceByType(availableDevices, product.deviceType);
        return devicesWithSameType && devicesWithSameType.filter(d => !_.includes(mandatoryProducts, d.id)).length > 0;
    }

    render() {
        //TODO: Manage most of it (or all of it) on backend side
        const mainEntries = [];
        this.props.entry.broadbandFixProduct && mainEntries.push(this.props.entry.broadbandFixProduct);
        this.props.entry.tvFixProduct && mainEntries.push(this.prepareTvEntry());
        this.props.entry.voipFixProduct && mainEntries.push(this.prepareVoipEntry());
        this.props.entry.vasPackagesB2BFixProduct && mainEntries.push(this.prepareVasPackagesB2BEntry());
        const availableDevices = this.props.configurables && this.props.configurables.length > 0 && this.props.configurables[0] && this.props.configurables[0].devices;
        const mandatoryProducts = this.props.configurables && this.props.configurables.length > 0 && this.props.configurables[0] && this.props.configurables[0].mandatoryProducts;
        //TODO: Backend should return the list with subentries and their names should come from classification attributes
        const subEntries = [];
        this.props.entry.modemFixProduct &&
        subEntries.push({
            ...this.props.entry.modemFixProduct,
            name: "Modem",
            description: this.props.entry.modemFixProduct.name,
            changeable: this.isDeviceChangeable(availableDevices, mandatoryProducts, this.props.entry.modemFixProduct),
            removable: isRemovable(this.props.configurables, mandatoryProducts, this.props.entry.modemFixProduct.code),
            section: "NEO_VASES"
        });
        this.props.entry.decoderFixProduct &&
        subEntries.push({
            ...this.props.entry.decoderFixProduct,
            name: "Dekoder",
            description: this.props.entry.decoderFixProduct.name,
            changeable: this.isDeviceChangeable(availableDevices, mandatoryProducts, this.props.entry.decoderFixProduct),
            removable: isRemovable(this.props.configurables, mandatoryProducts, this.props.entry.decoderFixProduct.code),
            section: "TV_VASES"
        });

        const descriptions = {
            deleteLabel: this.props.sectionConf.deleteEntryLabelMainItem,
            changeLabel: this.props.sectionConf.changeEntryLabel,
            detailsLabel: this.props.sectionConf.detailsLabel,
            changeDeviceLabel: this.props.descriptions.changeDevice,
            forFreeLabel: this.props.descriptions.deviceFreeWithinProposition ? this.props.descriptions.deviceFreeWithinProposition : "<div>Urządzenie bezpłatne</div><div>w ramach oferty</div>",
            serviceFreeLabel: this.props.descriptions.serviceFreeWithinProposition ? this.props.descriptions.serviceFreeWithinProposition : "<div>Bezpłatne w cenie</div><div>pakietu</div>",
        };

        return (
            <CommonPropositionEntry mainEntries={mainEntries}
                                    subEntries={subEntries}
                                    offerPrice={this.props.entry.priceForMainFixProducts}
                                    loyaltyLength={this.props.entry.loyaltyLength}
                                    bundleNo={this.props.entry.bundleNo}
                                    entryNo={this.props.entry.entryNo}
                                    onRemove={this.props.openRemovePopup}
                                    onDetails={this.onDetailsClicked}
                                    onChange={this.props.onVasChange}
                                    descriptions={descriptions}
                                    showNetPrices={this.props.showNetPrices}
                                    onVasRemove={this.props.onVasRemove}
                                    rejectionReason={this.props.entry.rejectionReason}
            />
        )
    }
}

const mapDispatchToProps = dispatch => ({
    openVoipModal: () => dispatch(voipSelection(true)),
    showTvModal: () => dispatch(changeTvModalVisibility(true)),
    showFixTvFilteredModal: () => dispatch(changeFixTvFilteredModalVisibility(true)),
    showB2BVasesModal: () => dispatch(changeB2BVasesModalVisibility(true))
});

const mapStateToProps = state => ({
    voipModalComponentIsMounted: voipModalComponentIsMounted(state)
});

export default connect(mapStateToProps, mapDispatchToProps)(FixMainProductComponent);